import * as Yup from "yup";
import axios from "../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import { Button, LinearProgress, Stack, Grid } from "@mui/material";
import AddBoxOutlinedIcon from "@mui/icons-material/AddBoxOutlined";
import { FormControl, InputLabel, MenuItem } from "@material-ui/core";
import { TextField, Select } from "formik-material-ui";
import { connect } from "react-redux";
const mapStateToProps = (state) => ({
  collegeID: state.collegeID,
});

class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userData: "",
      facultyList: [],
      facultyId: "",
    };
  }

  componentDidMount() {
    axios
      .get("api/departments/faculty/" + this.props.collegeID)
      .then((res) => {
        if (res.status === 200) {
          this.setState({
            facultyList: res.data,
          });
        }
      })
      .catch(function (error) {
        alert("Error occured:" + error);
      });
  }

  render() {
    return (
      <Grid container spacing={2}>
        <Grid item lg={12} xs={12} sm={12} md={12}>
          <Stack sx={{ width: "60%" }} spacing={2}>
            <h4>
              <AddBoxOutlinedIcon className="mr-2" />
              Add New Department
            </h4>
            <Formik
              initialValues={{
                Faculty_ID: "",
                Department_Name: "",
                Dept_Est_Year: "",
              }}
              validationSchema={Yup.object({
                Department_Name: Yup.string()
                  .max(200, "Must be 15 characters or less")
                  .min(3, "Must be at least 3 characters")
                  .required("Department Name Can't Be Left Blank"),
                Dept_Est_Year: Yup.string()
                  .max(200, "Must be 15 characters or less")
                  .min(4, "Must be at least 3 characters")
                  .required("Year Of Establishment of The Department Required"),
              })}
              onSubmit={(values, { setSubmitting, resetForm }) => {
                setTimeout(() => {
                  setSubmitting(false);
                  axios
                    .post(
                      "api/departments/" +
                        values.Faculty_ID +
                        "/" +
                        this.props.collegeID,
                      JSON.stringify(values),
                      {
                        headers: {
                          // Overwrite Axios's automatically set Content-Type
                          "Content-Type": "application/json",
                          Accept: "application/json",
                        },
                      }
                    )
                    .then((res) => {
                      if (res.status === 200) {
                        this.userCreate(res.data.resultID);
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                    });
                  resetForm();
                }, 500);
              }}
            >
              {({ submitForm, isSubmitting, values }) => (
                <Form>
                  <Stack spacing={2}>
                    <FormControl variant="outlined" fullWidth required>
                      <InputLabel htmlFor="faculty-select">
                        Select Faculty
                      </InputLabel>
                      <Field
                        component={Select}
                        label="Selct Faculty"
                        name="Faculty_ID"
                        inputProps={{
                          id: "faculty-select",
                        }}
                      >
                        {this.state.facultyList.map((faculty) => (
                          <MenuItem value={faculty.Faculty_ID}>
                            {faculty.Faculty_Name}
                          </MenuItem>
                        ))}
                      </Field>
                    </FormControl>

                    <Field
                      component={TextField}
                      name="Department_Name"
                      type="text"
                      label="Department Name"
                      variant="outlined"
                      fullWidth
                    />
                    <Field
                      component={TextField}
                      name="Dept_Est_Year"
                      type="text"
                      label="Year Of Establishment"
                      variant="outlined"
                      fullWidth
                    />
                    {isSubmitting && <LinearProgress />}
                  </Stack>
                  <Button
                    className="mt-3"
                    variant="contained"
                    color="primary"
                    disabled={isSubmitting}
                    onClick={submitForm}
                  >
                    Add Department
                  </Button>
                </Form>
              )}
            </Formik>
          </Stack>
        </Grid>
      
      </Grid>
    );
  }
}
export default connect(mapStateToProps)(index);
